import React from "react";
import ReactClipboard from "react-clipboard-simple";

export default function EmojiItem(props) {
  return (
    <ReactClipboard keyword={props.symbol}>
      <div
        style={{
          width: "200px",
          height: "200px",
          display: "grid",
          placeItems: "center",
          boxShadow: "5px 5px 5px #31d2f7",
          borderRadius: "20px",
          margin: "20px",
          border: "1px solid grey",
        }}
      >
        <h1>{props.symbol}</h1>
      </div>
    </ReactClipboard>
  );
}
