import React, { useState } from "react";
import EmojiContainer from "./Components/EmojiContainer";
import Header from "./Components/Header";
import data from "./Components/json/emoji.json";

function App() {
  const [emojisList, setEmojisList] = useState(data);

  const searchEmojies = (e) => {
    console.log(e.target.value);
    let search = data.filter((emoji) =>
      emoji.keywords.includes(e.target.value)
    );
    setEmojisList(search);
  };
  return (
    <div className="App">
      <Header />

      <div style={{ textAlign: "center" }}>
        <input
          type="text"
          name="Search"
          onChange={searchEmojies}
          style={{
            display: "inline-block",
            width: "50%",
            padding: "5px",
            borderRadius: "10px",
          }}
        />
      </div>
      <EmojiContainer emojisList={emojisList} />
    </div>
  );
}

export default App;
